@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Nunito+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito&family=Nunito+Sans&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  text-align: left;
  overflow-y: hidden;
}

h1 {
  padding: 0;
  margin-top: 0.4em;
  margin-left: 4.5vh;
  font-size: 30vh;
  color: var(--dark);
  text-align: justify;
  line-height: 120%;
  font-family: "Nunito", sans-serif;
  overflow-y: hidden;
}

@media (max-width: 1200px) {
  h1 {
    font-size: 15vh;
  }
}

@media (max-width: 750px) {
  h1 {
    font-size: 11vh;
  }
}

@media (max-width: 600px) {
  h1 {
    margin-top: 1em;
    font-size: 9.25vh;
  }
}

.container {
  display: grid;
}

.header {
  font-size: 10vh;
  color: var(--dark);
  text-align: left;
  margin-bottom: 0.6em;
  overflow-y: hidden;
}

.info {
  display: flex;
  text-align: center;
}

.info > img {
  padding: 0;
  flex: 1 1;
  height: 100%;
}

.text {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 0.5em;
  text-align: center;
}

.text > p {
  font-family: "Nunito", sans-serif;
  font-size: 4vh;
  line-height: 2;
  margin-left: 4.5vh;
  width: inherit;
  color: var(--dark-light);
  margin-bottom: 1.2em;
}

@media (max-width: 800px) {
  .text {
    width: 90%;
    text-align: center;
    float: center;
    overflow-x: hidden;
  }

  .info {
    display: flex;
    flex-direction: column;
    text-align: center;
  }

  .text > p {
    text-align: center;
    font-size: 2.25vh;
  }

  .info > img {
    max-width: 100%;
    padding: 3em;
    height: auto;
    float: center;
  }
}

@media (max-width: 700px) {
  .info {
    display: flex;
    flex-direction: column;
  }

  .info > img {
    max-width: 100%;
    padding: 3em;
    height: auto;
    float: center;
  }
}

.container {
  display: grid;
}

.header {
  font-size: 10vh;
  color: var(--dark);
  text-align: left;
  margin-bottom: 0.6em;
}

#background-image {
  -webkit-filter: saturate(99%) brightness(12.5%) hue-rotate(215.68deg);
          filter: saturate(99%) brightness(12.5%) hue-rotate(215.68deg);
  width: 32.5em;
  height: 21em;
  object-fit: cover;
  border-radius: 1.25em;
  margin-bottom: 1.75em;
}

/* #background-image:hover {
  filter: saturate(99%) brightness(25%) hue-rotate(95.68deg);
} */

.scroll_wrap {
  overflow-x: scroll;
  overflow-y: hidden;
  white-space: nowrap;
  margin-left: 4.5vh;
}

.project-item {
  position: relative;
  display: inline-block;
  width: 40em;
  height: 50%;
  margin-left: -3em;
  z-index: 0;
  overflow: hidden;
}

#title,
#description {
  position: absolute;
  background: none;
}

#title {
  top: 17.5%;
  width: 100%;
  padding-left: 1.5em;
  font-size: 3.75em;
  font-family: "Nunito", sans-serif;
  font-weight: 500;
  color: var(--background);
  text-align: left;
}

#description {
  font-size: 1.375em;
  color: var(--light);
  left: 10%;
  top: 47.5%;
  text-align: left;
  padding-left: 1.5em;
  width: 100%;
}

@media (max-width: 700px) {
  .header {
    font-size: 5vh;
    text-align: left;
  }

  .project-item {
    position: relative;
    display: inline-block;
    width: 20em;
    height: 50%;
    margin-left: -1.5em;
  }

  #background-image {
    -webkit-filter: saturate(99%) brightness(12.5%) hue-rotate(215.68deg);
            filter: saturate(99%) brightness(12.5%) hue-rotate(215.68deg);
    width: 15em;
    height: 21em;
    object-fit: cover;
    border-radius: 1.25em;
    margin-bottom: 1.75em;
  }

  .scroll_wrap {
    margin-left: 2.5vh;
    margin-right: 0;
    padding-right: 0;
  }

  #title {
    padding: 0;
    top: 17.5%;
    width: 100%;
    font-size: 2.5em;
    font-family: "Nunito", sans-serif;
    font-weight: 590;
    color: var(--background);
    text-align: center;
    white-space: pre-wrap;
    word-wrap: break-word;
    margin-bottom: 2em;
  }

  #description {
    font-size: 1.375em;
    color: var(--light);
    top: 47.5%;
    width: 9em;
    text-align: center;
    white-space: pre-wrap;
  }
}

#contact-header {
  text-align: center;
  font-size: 4em;
  margin-top: 1.5em;
  width: 97%;
}

#contact-text {
  color: var(--dark-light);
  text-align: center;
  width: 150%;
  white-space: pre-line;
  margin: 0;
  display: block;
}

.outer {
  position: relative;
  overflow-y: hidden;
}

.inner > a {
  position: absolute;
  top: 0;
  bottom: auto;
}

.inner > button {
  top: 0;
  bottom: auto;
}

.social-images > a:hover {
  opacity: 0.5;
}

.social-images > a > svg {
  margin-right: 2em;
  margin-left: 1em;
  x: 50%;
  width: 3.5em;
  height: auto;
  z-index: 1;
  fill: var(--icon-fill);
}

button {
  padding: 0.35em;
  text-align: center;
  font-size: 1.5em;
  text-decoration: none;
  border-radius: 0.45em;
  border-color: var(--light);
  border-style: solid;
  border-width: 1.5px;
  margin-top: 1.5em;
  width: 7.5em;
  height: 3em;
}

button > a {
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: var(--light);
  width: 100%;
  background-color: rgba(0, 0, 0, 0);
}

button:hover {
  color: var(--background);
  background-color: var(--dark);
  border-color: var(--background);
}

.footer {
  position: relative;
  width: 100%;
  height: auto;
  border: none;
  bottom: 0;
  top: 0.25em;
}

.footer > p {
  color: var(--light);
  background-color: var(--dark);
  padding: 1em;
  margin-top: 5em;
}

.social-images {
  margin-top: 2em;
}

.social-images > a {
  margin-right: 10px;
}

@media (max-width: 1000px) {
  #contact-text {
    width: 100%;
  }
  #contact-text-container {
    width: 100%;
    font-size: 3vh;
  }

  #contact-header {
    text-align: center;
    font-size: 3.5em;
    margin-top: 1.5em;
    margin-left: 0.2em;
  }
}

.nav {
  overflow: hidden;
  background: var(--background);
  position: fixed;
  top: 0;
  width: 100%;
  height: 2.5%;
  list-style-type: none;
  text-transform: lowercase;
  margin: 0em;
  padding: 1.25em;
  display: block;
  z-index: 5;
}

.nav > li {
  float: right;
  overflow: hidden;
  background: transparent;
  overflow: hidden;
  margin-right: 2em;
  width: auto;
  display: block;
}

.nav > li > a {
  padding: 1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: var(--light);
}

.nav > li > button {
  padding: 1.05em;
  text-align: center;
  font-size: 1.15em;
  text-decoration: none;
  color: var(--light);
}

.nav > li > a:hover {
  color: #005b96;
  cursor: pointer;
}

#home {
  float: left;
  font-weight: 20px;
  font-size: 1.15em;
}

#home > a,
a:hover {
  color: var(--dark-light);
}

#action {
  border-radius: 0.5em;
  border-color: var(--light);
  border-style: solid;
  border-width: 2px;
  margin-right: 4em;
}

#action:hover {
  border-color: var(--dark);
}

#action > a:hover {
  color: var(--background);
  background-color: var(--dark);
}

@media (max-width: 700px) {
  .nav {
    visibility: hidden;
  }
}

* {
  overflow-y: hidden;
}

/* Position and sizing of burger button */
.bm-burger-button {
  position: absolute;
  float: right;
  width: 5%;
  height: 2.5%;
  left: 85%;
  top: 4%;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: var(--dark);
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 50px;
  width: 50px;
  margin: 0.53em;
  background-color: var(--dark);
}

/* Color/shape of close button cross */
.bm-cross {
  background: var(--light);
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
  margin-right: -0.25em;
}

/* General sidebar styles */
.bm-menu {
  background: var(--dark);
  padding: 2em 1em 0;
  margin: 0;
  font-size: 1.15em;
  overflow-y: hidden !important;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #ffffff00;
}

/* Wrapper for item list */
.bm-item-list {
  background-color: var(--dark);
  padding: 0.8em;
}

/* Styling of overlay */
.bm-overlay {
  background-color: rgb(216, 54, 54);
}

/* Individual item */
.bm-item {
  display: inline-block;
  background-color: var(--dark);
  color: black;
  font-size: 2em;
  margin-top: 1em;
  text-align: left;
  text-decoration: none;
  transition: color 0.2s;
  overflow-y: hidden;
}

.bm-item > a {
  text-align: center;
  text-decoration: none;
  color: var(--light);
  background: transparent;
}

